<template>
    <div>
        <div class="row justify-content-center">
        <div class="col-sm-4 col-sm-offset-3">
        <label class="sr-only" for="projectname">Project Name</label>
        <input v-bind:class="textinput" type="text" id="projectname" v-model="newProjectName" title="prodjectname" placeholder=""><br>
        <label class="sr-only" for="adminid">Admin ID</label>
        <input v-bind:class="textinput" type="text" id="adminid" v-model="newProjectID" title="managerid" placeholder=" "><br>
        <label class="sr-only" for="adminpwd">Admin Password</label>
        <input v-bind:class="textinput" type="password" id="adminpwd" v-model="newProjectPass" title="managerpass" placeholder="  "><br>
        <label class="sr-only" for="adminpwd_confirm">Admin Password Confirm</label>
        <input v-bind:class="textinput" type="password" if="adminpwd_confirm" v-model="newProjectPassAgain" title="managerpassconfirm" placeholder="   "><br>
        <button v-bind:class="primebtn" @click="addProject"> </button> <!--      , props / emit   --> 
        </div>
    </div>
    </div>
</template>

<script>
import axios from 'axios'
import IP from '../../../static/IP.json'
export default{
    
    data(){
        return{
            newProjectName:'',
            newProjectID:'',
            newProjectPass:'',
            newProjectPassAgain:'',
            primebtn:'btn btn-primary disabled btn-block',
            textinput:'form-control'
        }
    },
    methods:{
        
        addProject(){
            var vm = this;
            console.log(this.newProjectName + ' ' + this.newProjectID + ' ' + this.newProjectPass + ' ' + this.newProjectPassAgain);
            //  ,  , MySQL .
            console.log(vm.newProjectName);
            axios.post('http://' + IP.IP+ ':7676/project/projectinfo/create', {projectname: vm.newProjectName
            }).then(res => { console.log(res.data)
            //this.dataset = res.data.payload;
                if(res.data.status == true)
                {
                    axios.post('http://' + IP.IP+ ':7676/project/admin/signup',{adminid: vm.newProjectID, adminpwd:vm.newProjectPass})
                    .then(res=>{
                        if(res.data.status == true)
                        {
                            this.$router.push(this.$route.query.redirect || '/main'); //-> Redirect  
                        }
                    })
                }
            })
            // Redirect 
            //this.$router.push(this.$route.query.redirect || '/main'); //-> Redirect  
        }
    }
}
</script>

